import ChartLine from "components/dashboards_components/ChartLine";
import ChartBar from "components/dashboards_components/ChartBar";
import PageVisitsCard from "components/dashboards_components/PageVisitsCard";
import TrafficCard from "components/dashboards_components/TrafficCard";
import Sidebar from "components/dashboards_components/Sidebar";
import { Redirect } from "react-router-dom";


export default function Dashboard() {
  fetch('/dashboard', {
    method: 'POST',
}).then(res => {
    res.json().then(data => {
      console.log(data)  
      if(data.error){

          return <Redirect to='/login' />
        }
  return (
    <div className="md:ml-64 bg-dashboard-background">
      <Sidebar />
      <div className="px-3 md:px-8 -mt-24">
        <div className="container mx-auto max-w-full">
          <div className="grid grid-cols-1 xl:grid-cols-5">
            <div className="xl:col-start-1 xl:col-end-4 px-4 mb-14">
              <ChartLine />
            </div>
            <div className="xl:col-start-4 xl:col-end-6 px-4 mb-14">
              <ChartBar />
            </div>
          </div>
        </div>
      </div>

      <div className="px-3 md:px-8 h-auto ">
        <div className="container mx-auto max-w-full">
          <div className="grid grid-cols-1 xl:grid-cols-5">
            <div className="xl:col-start-1 xl:col-end-4 px-4 mb-14">
              <PageVisitsCard />
            </div>
            <div className="xl:col-start-4 xl:col-end-6 px-4 mb-14">
              <TrafficCard />
            </div>
          </div>
        </div>
      </div>
    </div>
  );

})
}).catch(error => console.log(error))
}