import Card from '@material-tailwind/react/Card';
import CardHeader from '@material-tailwind/react/CardHeader';
import CardBody from '@material-tailwind/react/CardBody';
import CardFooter from '@material-tailwind/react/CardFooter';
import H5 from '@material-tailwind/react/Heading5';
import InputIcon from '@material-tailwind/react/InputIcon';
import { FaEye } from 'react-icons/fa';
import Input from '@material-tailwind/react/Input';
import Button from '@material-tailwind/react/Button';
import SimpleFooter from 'components/landing_components/SimpleFooter';
import Page from 'components/landing_components/login/Page';
import Container from 'components/landing_components/login/Container';
import Alert from "@material-tailwind/react/Alert";
import React, {useState} from 'react';



export default function Register() {

    const [name, setName] = useState('');
    const [mail, setMail] = useState('');
    const [pass, setPass] = useState('');
    const [repass, setRepass] = useState('');
    const [resp, setResp] = useState('');
    const [resCol, setResCol] = useState('lightGreen');
    const [togglep, setTogglep] = useState('password');
    const [toggler, setToggler] = useState('password');
    if(resp) {
        setTimeout(()=>{
            setResp('');
        }, 5000)
    }

    const addUser = async (n, m, p, r) => {
        if(name.trim() === '' || mail.trim() === '' || pass.trim() === '' || repass.trim() === '' ) {
            setResCol('deepOrange');
            setResp('Fill out all the Fields please!');
            return;
        } else {
            let user = {
                name: n,
                mail: m,
                pass: p,
                repass: r,
            }
            try {
                const response = await fetch('http://localhost:5000/register/add', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify(user)
                })
                if(response){
                    const ans = await response.json();
                    if(ans.message) {
                        //console.log(ans.message.errors[0].msg)
                    setResCol('deepOrange');
                    setResp(ans.message.errors[0].msg);
                    return;
                    } else {
                    setResCol('lightGreen')
                    setResp(ans);
                    setMail('');
                    setName('');
                    setPass('');
                    setRepass('');
                    return;
                }
            }
            }catch(error) {
                console.log(error);
                return;
            }
        }
    }
    

    return (
        <Page>
            {/* <DefaultNavbar /> */}
            <Container>
                <Card>
                    <CardHeader color="lightBlue">
                        <H5 color="white" style={{ marginBottom: 0 }}>
                            Register
                        </H5>
                    </CardHeader>

                    <CardBody>
                        <div className="mb-8 px-4">
                            <InputIcon
                                type="text"
                                color="lightBlue"
                                placeholder="Full Name"
                                iconName="account_circle"
                                onChange={e => setName(e.target.value)}
                                value={name}
                                required
                            />
                        </div>
                        <div className="mb-8 px-4">
                            <InputIcon
                                type="email"
                                color="lightBlue"
                                placeholder="Email Address"
                                iconName="email"
                                onChange={e => setMail(e.target.value)}
                                value={mail}
                                required
                            />
                        </div>
                        <div className="relative mb-8 px-4">
                            <Input
                                type={togglep}
                                color="lightBlue"
                                placeholder="Password"
                                name='pass'
                                iconName="lock"
                                onChange={e => setPass(e.target.value)}
                                value={pass}
                                required
                            />
                            <FaEye className='absolute bottom-2 right-5 text-lg cursor-pointer' color='grey' onClick={()=>setTogglep(togglep === 'password' ? 'text' : 'password')} />
                        </div>
                        <div className="relative mb-8 px-4">
                            <Input
                                type={toggler}
                                color="lightBlue"
                                placeholder="Re-password"
                                iconName="lock"
                                name='repass'
                                onChange={e => setRepass(e.target.value)}
                                value={repass}
                                required
                            />
                                <FaEye className='absolute bottom-2 right-5 text-lg cursor-pointer' color='grey' onClick={()=>setToggler(toggler === 'password' ? 'text' : 'password')} />

                        </div>
                    </CardBody>
                    <CardFooter>
                        <div className="flex justify-around">
                            <div color='lightBlue'>
                            <Button
                                ripple="dark"
                                onClick={() => addUser(name, mail, pass, repass)}
                            >
                                Register
                            </Button>
                            </div>
                            <a href='/login' color='lightBlue'><Button>Login here</Button></a>
                        </div>
                    </CardFooter>
                </Card>
                <div  className={resp ? 'mt-2' : 'invisible'}>
                <Alert color={resCol}>{resp}</Alert>
                </div>
            </Container>

            <SimpleFooter />
        </Page>
    );
}
