import { Switch, Route, Redirect } from "react-router-dom";
import Dashboard from "pages/Dashboard";
import AccountSettings from "pages/AccountSettings";
import MyInvoices from "pages/MyInvoices";
import Landing from "pages/Landing";
import Login from "pages/Login";
import Register from "pages/Register";
import { useReducer } from "react";
import { initialState, reducer } from "./components/useReducer"



// Tailwind CSS Style Sheet
import "assets/styles/tailwind.css";
import CreateInvoice from "pages/CreateInvoice";


function App() {
  const [state, dispatch] = useReducer(reducer, initialState);
 // console.log(state, dispatch)
  return (
    <>
      <div>
        <Switch>
          <Route exact path="/" component={Landing} />
          <Route exact path="/createinvoice" component={CreateInvoice} />
          <Route exact path="/login" component={Login} dispatch={dispatch} state={state}/>
          <Route exact path="/register" component={Register} />
          <Route exact path="/dashboard" component={Dashboard} state={state} />
          <Route exact path="/accountsettings" component={AccountSettings} />
          <Route exact path="/myinvoices" component={MyInvoices} />
          <Redirect from="*" to="/" />
        </Switch>
      </div>
    </>
  );
}

export default App;
